p, .btn {
    font-size: min(max(14px, 4vw),16px);
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
}

h1 {
    font-size: min(max(32px, 4vw),48px);
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 700;
}

h2 {
    font-size: min(max(26px, 4vw),36px);
    margin-bottom: 10%;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 700;
}

h3 {
    font-size: min(max(22px, 4vw),28px);
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
}

h4 {
    font-size: min(max(18px, 4vw),20px);
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 300;
}

.ijo {
    color: #28a745;
    margin-top: 10%;
    margin-bottom: 0px;
}


@media (min-width: 768px) {
    .col-md-6 {
        align-self: center;
    }
    .ijo {
        margin-top: 5%;
    }
    h2 {
        margin-bottom: 5%;
    }
}